import React from 'react'
import { connect } from 'react-redux'
import { startClock, serverRenderClock } from '../data/test/actions'
import Examples from '../components/examples'


function Index () {
  function getInitialProps ({ reduxStore, req }) {
   
    const isServer = !!req
    reduxStore.dispatch(serverRenderClock(isServer))

    return {}
  }

  this.prototype.componentDidMount = function componentDidMount () {
    const { dispatch } = this.props
    this.timer = startClock(dispatch)
  }

  this.prototype.componentWillUnmount = function componentWillUnmount () {
    clearInterval(this.timer)
  }

 
  return <Examples />
 
}

export default connect()(Index)
